body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: salmon;
  background-image: url(/clickygame/static/media/bg-tealcloud.a84b0149.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.myFont{
    font-size: 100px;
    -webkit-animation: fa-spin 6s infinite linear;
    animation: fa-spin 25s infinite linear;
  }

  .shake {
    -webkit-animation: shake 0.15s;
            animation: shake 0.15s; 
    -webkit-animation-iteration-count: 2; 
            animation-iteration-count: 2; 
}

@-webkit-keyframes shake {
    0% { -webkit-transform: translate(0px, 0px); transform: translate(0px, 0px); }
    50% { -webkit-transform: translate(4px, 0px); transform: translate(4px, 0px); }
    100% { -webkit-transform: translate(-4px, 0px); transform: translate(-4px, 0px); }
}

@keyframes shake {
    0% { -webkit-transform: translate(0px, 0px); transform: translate(0px, 0px); }
    50% { -webkit-transform: translate(4px, 0px); transform: translate(4px, 0px); }
    100% { -webkit-transform: translate(-4px, 0px); transform: translate(-4px, 0px); }
}

.nav-comp{
    padding-left: 30px !important;
    z-index: 1;
}
.nav-list{
    margin-right: 40px !important;
}
.icon-class{
  margin-top: 50px;
}
.card-instr{
  color: red !important;
  background-color: rgba(0,0,0,0);
  text-align: center;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

